const sliderAuto = (slider, miliseconds) => {
	slider.isLastSlide = () => {
	  return slider.page >= slider.dots.childElementCount - 1;
	}

	let slide = () => {
	  slider.slideTimeout = setTimeout(() => {
	    const slideTo = () => {
	      return slider.isLastSlide() ? 0 : slider.page + 1;
	    }

	    slider.scrollItem(slideTo(), true);
	  }, miliseconds);
	}

	slider.ele.addEventListener('glider-animated', (event) => {
	  window.clearInterval(slider.slideTimeout);
	  slide();
	});

	slide();
}

window.addEventListener('load', () => {
  const teamSlider = new Glider(document.querySelector('.team-slider'), {
    slidesToShow: 1,
    dots: '.dots',
    draggable: true,
    dragVelocity: 4,
    responsive: [
	    {
	      breakpoint: 498,
	      settings: {
	        slidesToShow: 2,
	        slidesToScroll: 2,
	        draggable: true,
	      }
	    },{
	      breakpoint: 1024,
	      settings: {
	        slidesToShow: 4,
	        slidesToScroll: 3,
	      }
    	}
	  ]
  })
  sliderAuto(teamSlider, 4000);
});

