const showOther = (btn, content) => {
  const showBtn = document.querySelector(btn),
        other = document.querySelector(content);

  other.classList.toggle('hidden-block');
  showBtn.addEventListener('click', () => {
    other.classList.toggle('hidden-block');
  });

}

if(locationPathName === '/card-edit.html' || 
  locationPathName === '/card-new.html' ||
  locationPathName === '/property.html' ||
  locationPathName === '/objects.html') {
  showOther('.display-other', '.other');
}

if(locationPathName === '/') {
  showOther('.about__btn', '.activity');
}